<template>
  <div class="musicItemRoot">
    <div class="itemMusicTop">
      <img :src="playlist.coverImgUrl" alt="" class="bgimg" />
      <div class="leftItem">
        <svg class="icon" aria-hidden="true" @click="$router.go(-1)">
          <use xlink:href="#icon-fanhui"></use>
        </svg>
        <span>歌单</span>
      </div>
      <div class="rightItem">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-sousuo-copy"></use>
        </svg>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-liebiao2-copy"></use>
        </svg>
      </div>
    </div>

    <div class="itemContentRoot">
      <div class="itemContentPic">
        <img :src="playlist.coverImgUrl" alt="" />
        <span>{{ changeCount(playlist.playCount) }}</span>
      </div>
      <div class="itemContent">
        <div class="itemContentTitle">
          <span>
            {{ playlist.name }}
          </span>
        </div>
        <div class="authorInfo">
          <div class="authorPic">
            <img :src="playlist.creator.avatarUrl" alt="" />
          </div>
          <!-- <div class="authorName">顾玲琳是我对象</div> -->
          <div class="authorName">{{playlist.creator.nickname}}</div>
          <div class="likeAuthor">
            <span>+ 关注</span>
          </div>
        </div>
        <div class="goal">
          <span>7.7 分</span>
        </div>
      </div>
      <div class="authorMore">
        <svg
          class="icon"
          aria-hidden="true"
          style="
            background-color: rgba(240, 248, 255, 0.14);
            border-radius: 10px;
            padding: 0 5px;
            height: 15px;
          "
        >
          <use xlink:href="#icon-a-xiajiantou1"></use>
        </svg>
      </div>
    </div>

    <div class="authorDesc">
      <span> 简介文：{{ playlist.description }} </span>
    </div>

    <div class="interaction">
      <div class="share">
        <div class="interaction-box">
          <span>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-a-fenxiang1"></use>
            </svg>
          </span>
          <p>{{playlist.shareCount}}</p>
        </div>
      </div>
      <div class="comment">
        <div class="interaction-box">
          <span>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-a-pinglun1"></use>
            </svg>
          </span>
          <p>{{playlist.commentCount}}</p>
        </div>
      </div>
      <div class="collection">
        <div class="interaction-box">
          <span>
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shoucangjia"></use>
            </svg>
          </span>
          <p>{{changeCount(playlist.subscribedCount)}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  setup(props) {
    // console.log(props);
    //props传值拿不到的话，就从session里面拿数据
    if(props.playlist.creator = ""){
      props.playlist.creator = JSON.parse(sessionStorage.getItem().playlist).creator
    }
  },
  methods: {
    //处理播放量数字
    changeCount(num) {
      if (num >= 100000000) {
        return (num / 10000000).toFixed(1) + "亿";
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
    },
  },
  props: ["playlist"],
};
</script>

<style lang="less" scoped>
.musicItemRoot {
  .itemMusicTop {
    width: 100%;
    height: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    // padding: 0.2rem;
    position: relative;
    .leftItem,
    .rightItem {
      width: 20%;
      height: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0 20px;
      span {
        font-size: 0.4rem;
        color: #fff;
      }
      .icon {
        fill: #fff;
      }
    }
    .bgimg {
      width: 100%;
      height: 11rem;
      position: fixed;
      z-index: -1;
      filter: blur(10px);
    }
    .itemContent {
      background: #a85454;
      width: 100%;
      height: 6rem;
    }
  }
  .itemContentRoot {
    width: 100%;
    // height: 3.5rem;
    padding: 10px;
    // background-color: #ffffff55;
    display: flex;
    justify-content: space-around;
    .itemContentPic {
      position: relative;
      width: 30%;
      img {
        width: 100%;
        border-radius: 10px;
      }
      span {
        position: absolute;
        color: #fff;
        font-size: 10px;
        top: 5px;
        right: 5px;
        font-weight: 700;
      }
    }
    .itemContent {
      width: 50%;
      .itemContentTitle {
        display: flex;
        span {
          color: #fff;
          font-weight: 700;
          font-size: 15px;
        }
      }
    }
    .authorInfo {
      width: 100%;
      padding: 7px 0;
      display: flex;
      justify-content: left;
      .authorPic {
        width: 25px;
        img {
          width: 100%;
          border-radius: 20px;
        }
      }
      .authorName {
        padding: 0px 5px;
        font-size: 13px;
        font-weight: 700;
        color: #ffffffce;
        line-height: 25px;
      }
      .likeAuthor {
        padding: 3px;
        span {
          border-radius: 10px;
          background-color: #ffffff18;
          color: #ffffffce;
          padding: 2px 5px;
          font-size: 5px;
          font-weight: 100;
        }
      }
    }
    .goal {
      span {
        padding: 2px 3px;
        font-size: 10px;
        color: #fff;
        background-color: rgba(255, 255, 255, 0.147);
        border-radius: 5px;
        box-sizing: border-box;
      }
    }
    .authorMore {
      padding: 0px 5px;
    }
  }
  .authorDesc {
    color: #ffffff5a;
    font-size: 10px;
    padding: 0 18px;
    //隐藏多余文字 只显示一行
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    word-break: break-all;
  }
  .interaction {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 20px 15px;
    .share,
    .comment,
    .collection {
      width: 30%;
      height: 38px;
      border-radius: 20px;
    }
    .share,
    .comment {
      background-color: #ffffff25;
      .interaction-box {
        position: relative;
        span {
          position: absolute;
          top: 9px;
          left: 25px;
        }
        p {
          position: absolute;
          color: #fff;
          top: 12px;
          left: 52px;
          font-size: 13px;
        }
      }
    }
    .collection {
      background-color: #a85454;
      .interaction-box {
        position: relative;
        span {
          position: absolute;
          top: 9px;
          left: 25px;
        }
        p {
          position: absolute;
          color: #fff;
          top: 12px;
          left: 52px;
          font-size: 13px;
        }
      }
    }
  }
}
</style>