<template>
  <div>
    <ItemMusic :playlist="state.playlist" />
    <ItemMusicList :alllist="state.alllist"/>
  </div>
</template>

<script>
import { useRoute } from "vue-router";
import { onMounted, reactive } from "vue";
import { getMusicItemList,getAllMusicList } from "@/axios/api/item.js";
import ItemMusic from "@/components/Item/ItemMusic.vue";
import ItemMusicList from "@/components/Item/itemMusicList.vue";

export default {
  setup() {
    const state = reactive({
      playlist: {},
      alllist:{},
    });
    onMounted(async () => {
      let id = useRoute().query.id;
      //获取各单详情
      let res = await getMusicItemList(id);
      // console.log(id);
      // console.log(res);
      state.playlist = res.data.playlist;

      // 获取歌单歌曲
      let result = await getAllMusicList(id);
      // console.log(result);
      state.alllist = result.data.songs;
      //防止页面刷新 DOM渲染完 但是数据没拿到 保存到sessionStorage
      sessionStorage.setItem("itemDetail", JSON.stringify(state));
    });
    return { state };
  },
  components: {
    ItemMusic,
    ItemMusicList
  },
};
</script>

